        </div> 
        
    </nav>
    <el-drawer
        v-model="drawer"
        :show-close="false"
        custom-class="nav-drawer"
        :with-header="false"
    >
        <div class="nav-search flex-1 flex-row-start align-center PL-20 PR-20">
            <el-icon color="#fff" :size="20" class="MR-10">
                <search />
            </el-icon>
            <input type="text" class="flex-1 text-20 nav-search__input" placeholder="Search market or enter a token access...">
        </div>
        <div class="flex-row-start MT-20">
            <div class="nav-btn text-white flex-row-start align-center pointer">
                <span class="MR-5">Dapp</span>
                <el-icon color="#fff" size=".2rem"><refresh /></el-icon>
            </div>
            <div class="nav-btn text-white flex-row-start align-center pointer MR-10 ML-10">
                <img src="~@/assets/images/common/money1.png" class="nav-btn__money"/>
                <span class="ML-10 color-text">$0.757</span>
            </div>
            <div class="nav-btn setting text-white flex-row-center align-center pointer">
                <img src="~@/assets/images/common/setting@2x.png" class="nav-btn__money" />
            </div>
        </div>
        
        <Meun :groupList="groupList" @click="goPath"/>
    </el-drawer>
</template>

<script>
import { CaretBottom, Search, Refresh, Menu } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import TabBar from './tabbar.vue'
import Meun from './Menu.vue';
export default {
    props: {
        groupList: Array
    },
    components: {
        CaretBottom,
        TabBar,
        Search,
        Refresh,
        Menu,
        Meun
    },
    data() {
        return {
            navList: [{
                name: 'Dashboard',
                icon: require('@/assets/images/common/dashboard.png'),
                activeIcon: require('@/assets/images/common/dashboard_active.png'),
                path: '/Dashboard'
            },{
                name: 'Gem',
                icon: require('@/assets/images/common/gem.png'),
                path: '/Gem'
            },{
                name: 'NFT',
                icon: require('@/assets/images/common/nft.png'),
                path: '/NFT'
            },{
                name: 'Invitation',
                icon: require('@/assets/images/common/gamefi.png'),
                activeIcon: require('@/assets/images/common/gamefi_active.png'),
                path: '/dapp/invitation'
            }],
            moreList: [{
                name: 'GameFi',
                icon: require('@/assets/images/common/gamefi.png'),
                activeIcon: require('@/assets/images/common/gamefi_active.png'),
                path: '/GameFi'
            }],
            account: '',
            connectLoading: false,
            drawer: false,
        }
    },
    computed: {
        getAccount() {
            if (this.account) {
                return '0x....'+ this.account.substr(-4)
            }
            return ''
        }
    },
    mounted(){
        this.account = this.$wallet.accounts[0]
        this.$wallet.on('accountChange', () => {
            this.account = this.$wallet.accounts[0]
        })
    },
    methods: {
        goPath(name) {
            this.drawer = false
            this.$emit('click', name)
        },
        logout() {
            this.$wallet.logout()
        },
        async connectHandler() {
            this.connectLoading = true
            try {
                await this.$wallet.initNetwork()
                this.connectLoading = false
            } catch (error) {
                this.connectLoading = false
            }
            
        },
        /**
         * 跳转到指定页面
         * @param {String} path 页面路由地址
         */
        togglePage(path) {
            if (path == this.$route.path) {
                this.$router.push({
                    path: '/redirect',
                    query: path
                })
                return
            }
            ElNotification({
                title: 'Coming soon',
                message: 'Coming soon',
            })
            return;
            if (!path) {
                return
            }
            this.$router.push({
                path
            })
        }
    }
}
</script>

